<template>
  <div class="user-info-container">
    <!--  -->
    <van-nav-bar
      title="个人信息"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
      fixed
    >
      <template slot="right">
        <span style="color: #fff;">保存</span>
      </template>
    </van-nav-bar>
    <!--  -->
    <van-cell-group class="user-info">
      <van-cell is-link title="头像" @click="isDialogShow = true">
        <template slot="default">
          <van-image width="35" height="35" round :src="user.photo" />
        </template>
      </van-cell>
      <van-cell is-link title="昵称" :value="user.name" @click="isNicknameShow = true" />
      <van-cell is-link title="介绍" value="内容" />
      <van-cell is-link title="性别" value="内容" />
      <van-cell is-link title="生日" value="内容" />
    </van-cell-group>

    <!-- 修改昵称 -->
    <van-popup
      class="nickname-show"
      v-model="isNicknameShow"
      position="bottom"
      :style="{ height: '30%' }"
    >
      <van-cell title="设置用户昵称" />
      <van-cell>
        <van-field class="nickname" v-model="user.name" placeholder="请输入用户昵称" />
      </van-cell>
      <van-cell>
        <div class="btn">
          <van-button type="primary" round size="small" @click="editUserProfile">修改</van-button>
          <van-button type="danger" round size="small">取消</van-button>
        </div>
      </van-cell>
    </van-popup>

    <!-- 修改用户头像 -->
    <upflie v-model="isDialogShow" @changemyimg="changemyimg" />
  </div>
</template>

<script>
import { userInfo, editProfile, editPhoto } from "@/api/user";
import upflie from '@/views/user_info/components/upflie.vue';
export default {
  data() {
    return {
      isNicknameShow: false,
      isDialogShow: false,
      user: {}
    };
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {},
    changemyimg() {

    },
    async getUserInfo() {
      let res = await userInfo();
      this.user = res;
    },
    async editUserProfile() {
      try {
        await editProfile({
          name: this.user.name
        });
        this.$toast.success("修改用户名成功");
      } catch (error) {
        console.log(error);
        this.$toast.fail("系统异常");
      } finally {
        this.isNicknameShow = false;
      }
    }
  },
  created() {
    this.getUserInfo();
  },
  components: {
    upflie
  }
};
</script>

<style lang="less" scoped>
.user-info-container {
  .user-info {
    margin-top: 46px;
  }

  .nickname-show {
    .btn {
      display: flex;
      justify-content: space-around;
    }

    .nickname {
      border: 1px solid #ccc;
      border-radius: 23px;
    }
  }
}
</style>