import request from '@/utils/request';

// 获取或刷新用户 token
function userLogin({ mobile, code }) {
  return request({
    method: 'POST',
    url: 'v1_0/authorizations',
    data: {
      mobile: mobile,
      code: code
    }
  });
}

// 取消拉黑用户
function cancelUserblacklist(id) {
  return request({
    method: 'DELETE',
    url: '/v1_0/user/blacklists/',
    params: {
      id
    }
  });
}

// 关注用户
function followUser(userid) {
  return request({
    method: 'POST',
    url: '/v1_0/user/followings',
    data: {
      target: userid
    }
  });
}

// 取消关注用户
function unfollowUser(userid) {
  return request({
    method: 'DETELE',
    url: `/v1_0/user/followings/${userid}`
  });
}

// 获取用户自己信息
function userInfo() {
  return request({
    method: 'GET',
    url: '/v1_0/user'
  });
}

// 编辑用户个人信息
function editProfile({ name }) {
  return request({
    method: 'PATCH',
    url: '/v1_0/user/profile',
    data: {
      name,
    }
  });
}

// 编辑用户头像
function editPhoto({ photo }) {
  // axios ---> 底层是用 XMLHttprequest 对象实现
  //    ----> 默认不允许传递文件
  //    ----> formDate
  // 将 photo 转换为 formDate
  let fd = new FormData();
  fd.append("photo", photo);
  return request({
    method: 'PATCH',
    url: '/v1_0/user/photo',
    data: fd
  })
}

export { userLogin, cancelUserblacklist, followUser, unfollowUser, userInfo, editProfile, editPhoto };