<template>
  <van-dialog
    :value="value"
    @input="$emit('input', $event)"
    title="标题"
    :showCancelButton="false"
    :showConfirmButton="false"
  >
    <van-cell-group>
      <van-cell title="从像册中选择" @click="openFile">
        <input type="file" ref="myfile" v-show="false" />
      </van-cell>
      <van-cell title="拍照" />
      <van-cell title="取消" @click="$emit('input', false)" />
    </van-cell-group>
  </van-dialog>
</template>

<script>
import { ImagePreview } from "vant";
import { editPhoto } from "@/api/user";
export default {
  data() {
    return {
      show: true
    };
  },
  methods: {
    openFile() {
      this.file.onchange = () => {
        let myfile = this.file.files[0];
        let reader = new FileReader();
        reader.addEventListener("load", res => {
          ImagePreview({
            images: [res.target.result],
            onClose: () => {
              this.$dialog
                .confirm({
                  title: "提示",
                  message: "是否需要将当前图片保存为头像"
                })
                .then(async () => {
                  let resData = await editPhoto({
                    photo: this.file.files[0]
                  });
                  this.$emit("changemyimg", resData);
                  this.$toast.success("头像修改成功");
                });
            }
          });
          this.$emit('input', false);
        });
        reader.readAsDataURL(myfile);
      };
      this.file.click();
    }
  },
  props: ["value"],
  computed: {
    file() {
      return this.$refs.myfile;
    }
  }
};
</script>

